import Link from "next/link";
import { FaHospitalSymbol, FaHeartbeat, FaHospitalAlt, FaPhoneAlt } from "react-icons/fa";
import type { IconType } from "react-icons";
import VideoSpotlight from "@/components/Home/Video";

const featureCards: Array<{
  title: string;
  description: string;
  icon: IconType;
  accent: string;
}> = [
  {
    title: "ศูนย์การแพทย์เฉพาะด้าน",
    description:
      "ทีมแพทย์ผู้เชี่ยวชาญครอบคลุมศัลยกรรม, อายุรกรรม และเลเซอร์ที่ผ่านการรับรองมาตรฐานสากล",
    icon: FaHospitalAlt,
    accent: "from-emerald-500 to-cyan-500",
  },
  {
    title: "ดูแลผู้ป่วย 24 ชั่วโมง",
    description:
      "หอผู้ป่วยทันสมัยพร้อมระบบ ICU/CCU และแผนกอุบัติเหตุฉุกเฉินที่พร้อมให้บริการตลอดวัน",
    icon: FaHeartbeat,
    accent: "from-rose-500 to-red-600",
  },
  {
    title: "ประสบการณ์มาตรฐานโลก",
    description:
      "กระบวนการการรักษาที่เชื่อมโยงกันระหว่างแพทย์ พยาบาล และทีมดูแลผู้ป่วย เพื่อผลลัพธ์ที่ไว้วางใจได้",
    icon: FaHospitalSymbol,
    accent: "from-sky-500 to-indigo-600",
  },
];

const stats = [
  { label: "ทีมแพทย์เฉพาะทาง", value: "150+" },
  { label: "เตียงผู้ป่วยรองรับ", value: "250" },
  { label: "เจ้าหน้าที่บริการ", value: "320" },
];

export default function RootPage() {
  return (
    <>
      <section className="relative isolate overflow-hidden bg-slate-900 text-white">
        <div className="absolute inset-0">
          <div className="h-full w-full bg-[url('/images/Home/WEB_BG.gif')] bg-cover bg-center opacity-70" />
          <div className="absolute inset-0 bg-gradient-to-br from-slate-950/90 via-slate-900/90 to-slate-950/90" />
        </div>
        <div className="relative z-10 mx-auto flex min-h-[70vh] max-w-6xl flex-col items-center justify-center gap-6 px-4 py-24 text-center">
          <p className="text-sm uppercase tracking-[0.4em] text-slate-300">BJH Bangkok Hospital</p>
          <h1 className="text-4xl font-semibold leading-tight text-white md:text-5xl">
            โรงพยาบาลบีเจเอช กรุงเทพฯ ดูแลสุขภาพด้วยจรรยาบรรณ และเทคโนโลยีระดับโลก
          </h1>
          <p className="max-w-2xl text-lg text-slate-200 md:text-xl">
            ประสบการณ์การรักษาที่ผสานแพทย์เฉพาะทาง บุคลากรที่อบอุ่น และนวัตกรรมการแพทย์ทันสมัย ให้คุณ
            และคนที่คุณรักไว้วางใจในทุกย่างก้าวของการดูแลสุขภาพ
          </p>
          <div className="flex flex-wrap items-center justify-center gap-4">
            <Link
              href="/login"
              className="rounded-full bg-gradient-to-r from-rose-500 to-pink-600 px-6 py-3 text-base font-semibold text-white shadow-lg shadow-rose-500/50 transition hover:brightness-110"
            >
              เข้าสู่ระบบสำหรับเจ้าหน้าที่
            </Link>
            <Link
              href="#services"
              className="rounded-full border border-white/70 px-6 py-3 text-base font-semibold text-white/90 transition hover:border-white hover:text-white"
            >
              ดูบริการคลินิกออนไลน์
            </Link>
          </div>
        </div>
      </section>

      <section
        id="services"
        className="bg-white text-slate-900 dark:bg-slate-950 dark:text-slate-100"
      >
        <div className="mx-auto max-w-6xl px-4 py-20">
          <div className="mb-12 text-center">
            <p className="text-sm uppercase tracking-[0.4em] text-slate-500">บริการของเรา</p>
            <h2 className="mt-3 text-3xl font-semibold text-slate-900 dark:text-white">
              ดูแลอย่างครบครันในทุกมิติสุขภาพ
            </h2>
            <p className="mx-auto mt-4 max-w-2xl text-base text-slate-600 dark:text-slate-300">
              ทีมแพทย์และฝ่ายบริการพร้อมดูแลด้วยมาตรฐานสากล, ห้องปฏิบัติการทันสมัย และคลินิกเฉพาะทางที่ตอบโจทย์
              ทุกกลุ่มอาการ
            </p>
          </div>
          <div className="grid gap-6 md:grid-cols-3">
            {featureCards.map((card) => (
              <article
                key={card.title}
                className="group rounded-3xl border border-slate-200 bg-gradient-to-b from-white to-slate-50 p-8 shadow-[0_15px_35px_rgba(15,23,42,0.08)] transition hover:-translate-y-1 hover:shadow-[0_25px_50px_rgba(15,23,42,0.15)] dark:border-slate-800 dark:from-slate-900 dark:to-slate-900"
              >
                <div
                  className={`mb-6 inline-flex items-center justify-center rounded-2xl bg-gradient-to-br ${card.accent} p-4 text-white`}
                >
                  <card.icon className="text-3xl" aria-hidden />
                </div>
                <h3 className="text-xl font-semibold text-slate-900 dark:text-white">
                  {card.title}
                </h3>
                <p className="mt-3 text-sm leading-relaxed text-slate-600 dark:text-slate-300">
                  {card.description}
                </p>
              </article>
            ))}
          </div>
        </div>
      </section>

      <section className="bg-gradient-to-br from-slate-900 via-slate-900/90 to-slate-800 text-white">
        <div className="mx-auto max-w-5xl px-4 py-16 text-center">
          <p className="text-sm uppercase tracking-[0.4em] text-slate-300">สถิติความเชื่อมั่น</p>
          <h2 className="mt-4 text-3xl font-semibold">ศูนย์การแพทย์ที่ทุกคนเลือกให้ความไว้วางใจ</h2>
          <div className="mt-10 grid gap-10 sm:grid-cols-3">
            {stats.map((stat) => (
              <div key={stat.label} className="rounded-2xl border border-white/20 bg-white/5 p-6">
                <p className="text-4xl font-bold text-white">{stat.value}</p>
                <p className="mt-2 text-sm uppercase tracking-wide text-slate-200">{stat.label}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="bg-slate-900 text-white">
        <VideoSpotlight />
      </section>

      <section className="bg-white text-slate-900">
        <div className="mx-auto max-w-6xl px-4 py-20">
          <div className="grid gap-10 md:grid-cols-3">
            <article>
              <p className="text-sm uppercase tracking-[0.4em] text-slate-500">ติดต่อเรา</p>
              <h3 className="mt-3 text-2xl font-semibold text-slate-900">BJH Bangkok Hospital</h3>
              <p className="mt-4 text-base text-slate-600">
                999 ถนนสุขุมวิท แขวงคลองตันเหนือ เขตวัฒนา กรุงเทพมหานคร 10110
              </p>
            </article>
            <article>
              <p className="flex items-center gap-2 text-sm uppercase tracking-[0.4em] text-slate-500">
                <FaPhoneAlt aria-hidden /> โทรศัพท์
              </p>
              <Link
                href="tel:+662xxx"
                className="mt-2 text-lg font-semibold text-slate-900 hover:text-slate-700"
              >
                +66 2 xxx xxxx
              </Link>
              <p className="mt-4 text-sm text-slate-600">ศูนย์บริการลูกค้าพร้อมตอบทุกคำถาม 24 ชั่วโมง</p>
            </article>
            <article>
              <p className="text-sm uppercase tracking-[0.4em] text-slate-500">อีเมล</p>
              <Link
                href="mailto:info@bjhbangkok.com"
                className="mt-2 text-lg font-semibold text-slate-900 hover:text-slate-700"
              >
                info@bjhbangkok.com
              </Link>
              <p className="mt-4 text-sm text-slate-600">ตารางนัดหมายและคำแนะนำด้านการเดินทาง</p>
            </article>
          </div>
        </div>
      </section>
    </>
  );
}

